* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.flexСontainer {
    border: 10px solid #7f00ff;
}

.flexItem {
}

.item1 {
    background-image: linear-gradient(45deg, #ff8177, #D62900);
}

.item2 {
    background-image: linear-gradient(45deg, #2cec82, #03eecb);
}

.item3 {
    background-image: linear-gradient(45deg, #be9be8, #d96969);
}

.item4 {
    background-image: linear-gradient(45deg, #25ec6f, #26f17e);
}

.item5 {
    background-image: linear-gradient(45deg, #45f185, #0fe38a);
}

.item6 {
    background-image: linear-gradient(45deg, #d0a2e1, #d695e0);
}

.item7 {
    background-image: linear-gradient(45deg, #364de3, #090cdc);
}

.item8 {
    background-image: linear-gradient(45deg, #e4fa84, #ef0606);
}

.flexСontainer {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    height: 20vh;
    align-items: flex-start;
    overflow: hidden;
}

.flexItem{
    min-width: 30vw;
    /*flex: 1 0 100%;*/
    flex-grow: 1;
}

